<template>
  <v-container fluid class="pa-0">
    <v-row no-gutters>
      <v-col cols="12" class="pa-0">
        レアリティ
      </v-col>
      <v-col
        cols="3"
        sm="1"
        v-for="rare in store.rarity"
        :key="rare"
        class="pa-0"
      >
        <v-checkbox
          v-model="store.search.cardList.rare"
          :label="rare"
          :value="rare"
          color="pink"
          hide-details
          :click="store.setOutputCardList()"
        ></v-checkbox>
      </v-col>
    </v-row>
    <hr class="mb-3">
    <v-row no-gutters>
      <v-col cols="12" class="pa-0">
        ムード
      </v-col>
      <v-col
        cols="6"
        sm="2"
        v-for="(moodName_ja, moodName_en) in store.mood"
        :key="moodName_ja"
        class="pa-0"
      >
        <v-checkbox
          v-model="store.search.cardList.mood"
          :value="moodName_en"
          color="pink"
          hide-details
          :click="store.setOutputCardList()"
        >
          <template v-slot:label>
            <v-img
              :src="require(`@/assets/mood_icon/icon_${moodName_en}.png`)"
              class="icon mood"
            ></v-img>{{ moodName_ja }}
          </template>
        </v-checkbox>
      </v-col>
    </v-row>
    <hr class="mb-3">
    <v-row no-gutters>
      <v-col cols="12" class="pa-0">
        タイプ
      </v-col>
      <v-col
        cols="12"
        sm="3"
        md="3"
        lg="2"
        xl="2"
        v-for="(styleTypeName_ja, styleTypeName_en) in store.styleType"
        :key="styleTypeName_ja"
        class="pa-0"
      >
        <v-checkbox
          v-model="store.search.cardList.styleType"
          :value="styleTypeName_en"
          color="pink"
          hide-details
          :click="store.setOutputCardList()"
        >
          <template v-slot:label>
            <v-img
              :src="require(`@/assets/styleType_icon/icon_${styleTypeName_en}.png`)"
              class="icon type"
            ></v-img>{{ styleTypeName_ja }}
          </template>
        </v-checkbox>
      </v-col>
    </v-row>
    <hr class="mb-3">
    <v-row no-gutters>
      <v-col cols="12" class="pa-0">
        メンバー
      </v-col>
      <v-col
        cols="12"
        sm="4"
        md="3"
        lg="2"
        xl="2"
        v-for="(name_ja, name_en) in store.charactorName"
        :key="name_ja"
        class="pa-0"
      >
        <v-checkbox
          v-model="store.search.cardList.memberName"
          :value="name_en"
          color="pink"
          hide-details
          :click="store.setOutputCardList()"
        >
          <template v-slot:label>
            <v-img
              :src="require(`@/assets/member_icon/icon_SD_${name_en}.png`)"
              class="icon member"
            ></v-img>{{ name_ja.first }} {{ name_ja.last }}
          </template>
        </v-checkbox>
      </v-col>
    </v-row>
    <hr class="mt-1 mb-3">
    <v-row no-gutters>
      <v-col cols="12" class="pa-0 mb-8">
        カードレベル
      </v-col>
      <v-col cols="12">
        <v-range-slider
          hide-details
          v-model="store.search.cardList.cardLevel"
          max="120"
          min="0"
          thumb-label="always"
          step="1"
          color="pink"
          thumb-color="pink"
          :change="store.setOutputCardList()"
          class="px-2"
        ></v-range-slider>
      </v-col>
    </v-row>
    <hr class="my-3">
    <v-row no-gutters>
      <v-col cols="12" class="pa-0 mb-8">
        スペシャルアピールレベル
      </v-col>
      <v-col cols="12">
        <v-range-slider
          hide-details
          v-model="store.search.cardList.SALevel"
          max="14"
          min="1"
          thumb-label="always"
          step="1"
          color="pink"
          thumb-color="pink"
          :change="store.setOutputCardList()"
          class="px-2"
        ></v-range-slider>
      </v-col>
    </v-row>
    <hr class="my-3">
    <v-row no-gutters>
      <v-col cols="12" class="pa-0 mb-8">
        スキルレベル
      </v-col>
      <v-col cols="12">
        <v-range-slider
          hide-details
          v-model="store.search.cardList.SLevel"
          max="14"
          min="1"
          thumb-label="always"
          step="1"
          color="pink"
          thumb-color="pink"
          :change="store.setOutputCardList()"
          class="px-2"
        ></v-range-slider>
      </v-col>
    </v-row>
    <hr class="my-3">
    <v-row no-gutters>
      <v-col cols="12" class="pa-0 mb-8">
        解放レベル
      </v-col>
      <v-col cols="12">
        <v-range-slider
          hide-details
          v-model="store.search.cardList.releaseLevel"
          max="5"
          min="1"
          thumb-label="always"
          step="1"
          color="pink"
          thumb-color="pink"
          :change="store.setOutputCardList()"
          class="px-2"
        ></v-range-slider>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: 'CardListFilter'
}
</script>

<script setup>
  import { useStoreCounter } from '../stores/counter';
  const store = useStoreCounter();
</script>

<style lang="scss" scoped>
.icon {
  margin-right: 5px;

  &.mood {
    width: 30px;
  }

  &.type {
    width: 30px;
  }

  &.member {
    width: 35px;
  }
}
</style>